'use client';

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import { 
  Menu, X, LogOut, Globe, Scale, HelpCircle, 
  Home, Database, BookOpen, Cpu, ShieldCheck 
} from 'lucide-react';
import { supabase } from '@/lib/supabase';

export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false);
  const [user, setUser] = useState<any>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Sinkronisasi status login user secara real-time
    const syncAuth = async (currentUser: any) => {
      setUser(currentUser ?? null);
      setLoading(false);
    };

    // Cek user saat pertama kali load
    supabase.auth.getUser().then(({ data: { user } }) => syncAuth(user));

    // Listen perubahan auth (login/logout)
    const { data: { subscription } } = supabase.auth.onAuthStateChange((_event, session) => {
      syncAuth(session?.user);
    });

    return () => subscription.unsubscribe();
  }, []);

  const handleSignOut = async () => {
    await supabase.auth.signOut();
    window.location.href = '/';
  };

  return (
    <nav className="border-b border-gray-100 py-6 px-8 bg-white/80 backdrop-blur-xl sticky top-0 z-[100] no-print text-black">
      <div className="max-w-7xl mx-auto flex justify-between items-center">
        
        {/* 1. LOGO */}
        <Link href="/" className="group flex items-center gap-3">
          <div className="w-12 h-12 bg-black rounded-xl flex items-center justify-center group-hover:bg-[#D4AF37] transition-all">
            <span className="text-white font-serif text-2xl italic font-bold group-hover:text-black">G</span>
          </div>
          <div className="text-2xl font-serif tracking-[0.2em] font-bold uppercase">
            Global<span className="text-[#D4AF37]">Tax</span>
            <span className="block text-[12px] tracking-[0.5em] text-gray-400 font-sans -mt-1 uppercase">Laboratory</span>
          </div>
        </Link>
        
        {/* 2. DESKTOP NAV (3 Utama: Encyclopedia, Intelligence, AI Vault) */}
        <div className="hidden md:flex items-center gap-8">
          {/* Menu Encyclopedia (Daftar Negara) */}
          <Link href="/encyclopedia" className="text-[12px] font-black uppercase tracking-widest text-gray-400 hover:text-black transition-colors flex items-center gap-2">
            <BookOpen size={14} className="text-[#D4AF37]" /> Encyclopedia
          </Link>

          {/* Menu Intelligence (Search & AdSense) */}
          <Link href="/intelligence" className="text-[12px] font-black uppercase tracking-widest text-gray-400 hover:text-black transition-colors flex items-center gap-2">
            <Database size={14} className="text-[#D4AF37]" /> Intelligence
          </Link>
          
          {loading ? (
             <div className="w-20 h-8 bg-gray-50 animate-pulse rounded-full"></div>
          ) : !user ? (
            /* Tampilan jika BELUM Login */
            <div className="flex items-center gap-4 border-l border-gray-100 pl-8">
              <Link href="/auth/login" className="text-[12px] font-black uppercase tracking-widest text-black hover:text-[#D4AF37] transition-colors">
                Sign In
              </Link>
              <Link href="/auth/register" className="bg-black text-[#D4AF37] px-8 py-3 rounded-full text-[12px] font-black uppercase tracking-widest hover:bg-[#D4AF37] hover:text-black transition-all shadow-xl shadow-gold/10">
                Register
              </Link>
            </div>
          ) : (
            /* Tampilan jika SUDAH Login (Fokus AI Vault) */
            <div className="flex items-center gap-6 border-l border-gray-100 pl-8">
              {/* Menu AI Vault (Pengganti Command Center/Pro) */}
              <Link href="/ai-vault" className="flex items-center gap-2 text-black font-black text-[11px] uppercase tracking-widest bg-[#D4AF37] px-6 py-2.5 rounded-full shadow-lg shadow-gold/20 hover:scale-105 transition-transform">
                <Cpu size={14} /> AI Tax Vault
              </Link>
              
              <button onClick={handleSignOut} className="text-gray-400 hover:text-red-500 transition-colors" title="Sign Out">
                <LogOut size={20} />
              </button>
            </div>
          )}
        </div>

        {/* MOBILE TOGGLE */}
        <button className="md:hidden" onClick={() => setIsOpen(!isOpen)}>
          {isOpen ? <X size={28} /> : <Menu size={28} />}
        </button>
      </div>

      {/* 3. MOBILE MENU OVERLAY */}
      {isOpen && (
        <div className="absolute top-full left-0 w-full bg-white border-b border-gray-100 p-8 flex flex-col gap-8 md:hidden shadow-2xl animate-in slide-in-from-top duration-300">
          <div className="flex flex-col gap-6">
            <p className="text-[10px] font-black uppercase tracking-[0.3em] text-gray-300">Navigation</p>
            <Link href="/encyclopedia" onClick={() => setIsOpen(false)} className="flex items-center gap-4 text-[14px] font-bold uppercase tracking-widest text-black">
              <BookOpen size={18} className="text-[#D4AF37]" /> Encyclopedia
            </Link>
            <Link href="/intelligence" onClick={() => setIsOpen(false)} className="flex items-center gap-4 text-[14px] font-bold uppercase tracking-widest text-black">
              <Database size={18} className="text-[#D4AF37]" /> Intelligence
            </Link>
            {user && (
              <Link href="/ai-vault" onClick={() => setIsOpen(false)} className="flex items-center gap-4 text-[14px] font-bold uppercase tracking-widest text-[#D4AF37]">
                <Cpu size={18} /> AI Tax Vault
              </Link>
            )}
          </div>

          <hr className="border-gray-50" />

          {!user ? (
            <div className="flex flex-col gap-4">
              <Link href="/auth/login" onClick={() => setIsOpen(false)} className="w-full py-5 border border-gray-100 rounded-xl text-[14px] font-bold uppercase tracking-widest text-center text-black">
                Sign In
              </Link>
              <Link href="/auth/register" onClick={() => setIsOpen(false)} className="w-full bg-black text-[#D4AF37] py-5 rounded-xl text-[14px] font-black uppercase tracking-widest text-center shadow-xl shadow-gold/10">
                Get Started
              </Link>
            </div>
          ) : (
            <div className="flex flex-col gap-4">
              <button onClick={handleSignOut} className="w-full py-4 text-red-500 font-bold uppercase tracking-widest text-[12px] flex items-center justify-center gap-2 border border-red-50 rounded-xl">
                <LogOut size={16} /> Sign Out
              </button>
            </div>
          )}
        </div>
      )}
    </nav>
  );
}